import React from 'react'
import './Toolbar.css'
import AvatarPic from '../../../assets/icons/Avatar.svg'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: '#F9FAFB',
        border: '1px solid #F9FAFB',
        fontFamily: 'IRANSans',
        fontWeight: 400,
        fontSize: '18px',
        lineHeight: '30px',
        color: '#333333',
        margin: 'auto',
        marginTop: '24px',
        padding: '5px 8px 5px 0',
        '&:hover' :{
            cursor: 'pointer',
            width: '100%',
            backgroundColor:'#C3F0FF',
            border: '1px solid #8BD5F4',
        }
    },
    selected: {
        '&.Mui-selected' : {
            fontFamily : "IRANSans",
            fontWeight:'bold',
            fontSize : '18px',
            color: '#4975A1',
            backgroundColor : "#F9FAFB",
            border: '1px solid #F9FAFB',
        }
    }
}));

const Toolbar = (props) => {
    const classes = useStyles();

    return(
        <React.Fragment>
            <div className='toolbar-main-div'>
                <div className='toolbar-avatar-div'>
                    <img src={AvatarPic} alt='AvatarPic'/>
                    <p>user name</p>


                </div>
                <div className='toolbar-credit-div'>
                    <p>اعتبار کیف پول</p>
                    <p>۰ تومان</p>
                </div>
                <List>
                    <ListItem
                        autoFocus
                        className={clsx(classes.root,classes.selected)}
                        button
                        onClick={props.firstButtonHandler}
                        selected={props.selectedIndex===0}
                    >
                        مشاهده وضعیت شما</ListItem>
                    <ListItem
                        className={clsx(classes.root,classes.selected)}
                        button
                        onClick={props.secondButtonHandler}
                        selected={props.selectedIndex===1}>
                        پرداخت بدهی</ListItem>
                    <ListItem
                        className={clsx(classes.root,classes.selected)}
                        button
                        onClick={props.thirdButtonHandler}
                        selected={props.selectedIndex===2}
                    >
                        افزایش اعتبار کیف پول</ListItem>
                    <ListItem
                        className={clsx(classes.root,classes.selected)}
                        button
                        onClick={props.fourthButtonHandler}
                        selected={props.selectedIndex===3}
                    >
                        تبادل پول بین کیف پول ها</ListItem>
                    <ListItem
                        className={clsx(classes.root,classes.selected)}
                        button
                        onClick={props.fifthButtonHandler}
                        selected={props.selectedIndex===4}
                    >
                        حذف مدیریت شده ی گروه</ListItem>
                    <ListItem
                        className={clsx(classes.root,classes.selected)}
                        button
                        onClick={props.sixthButtonHandler}
                        selected={props.selectedIndex===5}
                        style={{marginBottom:'16px'}}>
                        ملحق شدن به گروه</ListItem>
                </List>
            </div>
        </React.Fragment>
    )
}
export default Toolbar
