import React from 'react'
import HomePage from '../src/pages/HomePage'
import Condition from '../src/pages/Condition'
import SignupPage from '../src/pages/SignupPage'
import SigninPage from '../src/pages/SigninPage'
import  {HashRouter ,Route} from 'react-router-dom'
import ProfilePage from "./pages/ProfilePage";
import Redirect from "react-router-dom/es/Redirect";

const App = () => {



  return(
  <React.Fragment>
    <HashRouter >
      <Route path='/' exact  component={HomePage}/>
      <Route path='/condition' exact component={Condition}/>
      <Route path={'/signup'} exact component={SignupPage}/>
      <Route path={'/signin'} exact component={SigninPage}/>
      <Route path={'/profile'} exact component={ProfilePage}/>
      <PrivateRoute path={'/invite'} exact component={ProfilePage}/>
      <PrivateRoute path={'/record-purchase-invoices'} exact component={ProfilePage}/>
      <PrivateRoute path={'/create-group'} exact component={ProfilePage}/>

    </HashRouter >
  </React.Fragment>
  )
}

const isLogin = () => localStorage.getItem("isLogin");


const PrivateRoute = ({ component: Component, ...rest }) => {

  return <Route {...rest} render={(props) => {
        if(isLogin === "true"){
         return <Component {...props} />
        }
        else{
          return <Redirect to='/signin' />
        }
      }
  }
  />
}

export default App;




