import React, {useEffect, useState} from 'react'
import Header from '../components/header/Header'
import Toolbar from '../components/ui/toolbar/Toolbar'
import Footer from '../components/footer/Footer'
import AboveFooter from '../components/ui/above-footer/AboveFooter'
import IncreaseCredit from '../components/increase-credit/IncreaseCredit'
import "./ProfilePage.css"
import {dashboardApi} from "../connect/dashboardApi";
import {useHistory} from "react-router-dom";
const ProfilePage = () => {

    const history = useHistory();
    const [selectedIndex, setSelectedIndex] = React.useState(0);

    const [user,setUser] = useState({name:"",username:"",email:"",phoneNumber:"",walletAmount:""});

    const handleListItemClick = (event, index) => {
        setSelectedIndex(index);
      };

    const firstButtonHandler = (event)=>handleListItemClick(event,0);
    const secondButtonHandler = (event)=>handleListItemClick(event,1);
    const thirdButtonHandler = (event)=>handleListItemClick(event,2);
    const fourthButtonHandler = (event)=>handleListItemClick(event,3);
    const fifthButtonHandler = (event)=>handleListItemClick(event,4);
    const sixthButtonHandler = (event)=>handleListItemClick(event,5);

    useEffect(()=>{
        if(localStorage.getItem("isLogin") === "false" || localStorage.getItem("isLogin") === null){
            history.push('/signin');
        }
        dashboardApi ((isOK,data) => {
            if(isOK){
                setUser({...user ,name : data.name , username: data.username,walletAmount: data.walletAmount,email: data.email});
                alert(data.email);
            }else{
                alert(data);
            }
        })
    },[]);

    return(
        <React.Fragment>
            <div style={{backgroundColor:'#F9FAFB'}}>
                <div className="main-header">
                    <Header/>
                </div>
                <div className="profile-page-main-div">
                    <div></div>
                    <Toolbar
                        user = {user}
                    selectedIndex = {selectedIndex}
                    firstButtonHandler = {firstButtonHandler}
                    secondButtonHandler = {secondButtonHandler}
                    thirdButtonHandler = {thirdButtonHandler}
                    fourthButtonHandler = {fourthButtonHandler}
                    fifthButtonHandler = {fifthButtonHandler}
                    sixthButtonHandler = {sixthButtonHandler}
                    />
                    <div></div>
                    <div>
                        {selectedIndex === 2 && <IncreaseCredit/>}

                    </div>
                    <div></div>
                </div>
                <AboveFooter/>
                <Footer/>
            </div>
        </React.Fragment>
    )
}
export default ProfilePage
