import React, {useEffect, useState} from 'react'
import './Header.css'
import LogoPic from '../../assets/icons/logo.svg'
import {Link,useHistory} from 'react-router-dom'
import {logoutApi} from "../../connect/logoutApi";

const Header = () => {

    const [isLogin , setIsLogin] = useState(false);
    const history = useHistory();
    const logoHandler = () => {
        history.push('/')
    }

    useEffect(()=>{
        if(localStorage.getItem("isLogin")===null || localStorage.getItem("isLogin") === "false"){
            setIsLogin(false);
        }else {
            setIsLogin(true);
        }
    },[localStorage.getItem("isLogin")])



    const logoutHandler = ()=>{
        logoutApi((isOk,date)=>{
            if(isOk){
                localStorage.setItem("isLogin","false");
                history.push('/');
            }else{
                localStorage.setItem("isLogin","true");
                alert(date);
            }
        })
    }

    const checkLogin = ()=> {
        if(isLogin){
            return <button className="logout-button" onClick={logoutHandler}>خروج</button>

        }else{
            return <button className='header-toolbar-enter-button'><Link className='header-toolbar-enter-link' to='/signin' >ورود به حساب کاربری</Link></button>
        }
    }

    return(
        <React.Fragment>

                <div className='header-main-div'>
                    <img src={LogoPic} style={{cursor:'pointer'}} onClick={logoHandler} alt='logo'/>
                    <div></div>
                    <div className='header-toolbar-div'>
                        <Link to='/create-group' className='header-toolbar-link' >تشکیل گروه</Link>
                        <Link to='/record-purchase-invoices' className='header-toolbar-link'>ثبت فاکتور های خرید</Link>
                        <Link to='/profile' className='header-toolbar-link'>پروفایل شما</Link>
                        <Link to='/invite' className='header-toolbar-link'>دعوت از دیگران</Link>
                    </div>
                    <div></div>
                    <div style={{direction:'ltr',display:'flex',alignItems:'center'}}>
                        {checkLogin()}

                    </div>
                </div>

        </React.Fragment>
    )
}
export default Header
